export default "/* Pagination CSS */\n.paginationFooter {\n  background: #ffffff 0% 0% no-repeat padding-box;\n  box-shadow: 0px -5px 6px #00000029;\n  position: relative;\n  display: inline-block;\n  width: 100%;\n}\n.paginationFooter .paginationTotalCount {\n  background: #ecf5f8;\n  border-radius: 15px;\n  color: #373737;\n  font: normal normal normal 14px/1 Helvetica Neue;\n  padding: 0.3rem 1.3rem;\n  display: inline-block;\n}\n.paginationFooter .submitBottomBtn {\n  background: url('submitBtn.png') transparent no-repeat 100%;\n  background-position: center;\n  width: 100%;\n  background-size: auto;\n  margin-top: 0rem;\n}\n.paginationFooter .submitBottomBtn .submitBtn {\n  background: transparent;\n  font-size: 15px;\n  line-height: 33px;\n  padding: 0px;\n  width: calc(100% - 200px);\n  cursor: pointer;\n  box-shadow: none;\n}\n.paginationFooter .submitBottomBtn .submitBtn:disabled {\n  cursor: not-allowed;\n}\n.paginationFooter .customPagination {\n  /* padding: 0.3rem 1.3rem; */\n  display: flex;\n  justify-content: flex-end;\n}\n.paginationFooter .customPagination .pagination {\n  font-family: Helvetica, FontAwesome;\n  margin: 5px 0px;\n}\n.paginationFooter .customPagination .pagination .page-item {\n  display: none;\n}\n.paginationFooter .customPagination .pagination .page-item:first-child,\n.paginationFooter .customPagination .pagination .page-item:nth-child(2),\n.paginationFooter .customPagination .pagination .page-item:last-child {\n  display: block;\n}\n.paginationFooter .customPagination .pagination .page-item {\n  text-align: center;\n  border-radius: 0px;\n}\n.paginationFooter .customPagination .pagination .page-item a {\n  background: #7044cd;\n  color: #fff;\n  border: none;\n  padding: 0.3rem 1.3rem;\n}\n.paginationFooter .customPagination .pagination .page-item a:focus {\n  box-shadow: none;\n}\n.paginationFooter .customPagination .pagination .page-item:first-child a {\n  border-bottom-left-radius: 15px;\n  border-top-left-radius: 15px;\n}\n.paginationFooter .customPagination .pagination .page-item:last-child a {\n  border-bottom-right-radius: 15px;\n  border-top-right-radius: 15px;\n}\n.paginationFooter .customPagination .pagination .page-item:nth-child(2) a {\n  background: #ecf5f8;\n  color: #3a3b3b;\n  padding: 0.3rem 0.5rem;\n}\n.paginationFooter .customPagination .pagination .page-item.disabled a {\n  background: #def0f6;\n  cursor: not-allowed;\n}\n/*# sourceMappingURL=data:application/json;base64, */";